.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin-top: 1rem;

  .card {
    overflow: hidden;
    position: relative;
    opacity: 0.8;
    img {
      width: 250px;
      height: 300px;
      border-radius: 1rem;
    }

    &:hover {
      opacity: 1;
    }
    &:hover h2 {
      transform: translateX(0px);
    }
    &:hover .price {
      display: none;
    }

    h2 {
      width: 250px;
      transform: translateX(300px);
      transition: all 0.7s;
      position: absolute;
      bottom: 0px;
      //   left: 3px;
      color: white;
      font-weight: bold;
      background-color: rgba(0, 0, 0, 0.7);
      padding: 10px 0px;
      border-radius: 1rem;
    }
    .price {
      font-weight: bold;
      color: white;
      background-color: black;
      width: fit-content;
      padding: 10px;
      border-radius: 10px;
      position: absolute;
      bottom: 10px;
      left: 5.8rem;
    }
  }
}
