.container {
  text-align: center;
  margin-top: 2rem;

  h1 {
    color: purple;
    font-weight: bold;
    font-size: 3rem;
    margin-bottom: 1rem;
  }
  .links {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    align-items: center;
    a {
      text-decoration: none;
      color: white;
      background-color: fuchsia;
      padding: 1rem;
      border-radius: 10px;
      font-weight: bold;
      &:hover {
        background-color: rgb(252, 1, 126);
      }
    }
  }
}
